#intro{
    height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    overflow: hidden;
    position: relative;
}
.bg{
    position: absolute;
    z-index: -1;
    top: -4rem;
    right: -15rem;
    object-fit: cover;
    height: 80vh;
}

.introContent{
    height: 100vh;
    width: 100vw;
    padding: 2rem;
    font-size: 3rem;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.hello{
    font-size: 2.6rem;
    font-weight: 100;
}
.introName{
    color: yellow;
}
.introPara{
    font-size: 1.5rem;
    font-weight: 300;
    letter-spacing: 0.5px;
}
.btn{
    background: white;
    margin: 1rem 0;
    padding: 0.75rem 1.5rem;
    border: none;
    border-radius: 2rem;
    font-size: 1.5rem;
}
.btnImg{
    object-fit: cover;
    margin: 0 0.25rem;
    height: 1.5rem;
}
@media screen and (max-width:840px){
    .bg{
        right: -10vw;
    }
    .introContent{
        font-size: 8vw;
    }
    .hello{
        font-size: 4.5vw;
    }
}
@media screen and (max-width:520px){
    .bg{
        right: -29vw;
    }

}